.menu {
  text-align: -webkit-center;
  background-color: black;
  height: 100%;
  background-image: url("https://dmitry-skrypka.github.io/Incoder/img/menu_logo.jpg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  padding-top: 10px;
  margin-top: 40px;
  -webkit-animation: fadein 3s ease-in alternate;
  animation: fadein 3s ease-in alternate;
}
.menu_buttons_container {
  /*display: flex;*/
  /*justify-content: center;*/
  text-align: center;

  text-align: -moz-center;
  padding-bottom: 76px;
  text-align: -webkit-center;
}

.menu_button {
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 15px;
  background-color: rgba(244, 255, 245, 0.14);
  width: 300px;
}

.menu_logo {
  background-image: url("https://dmitry-skrypka.github.io/Incoder/img/Incoded.png");
  width: 300px;
  height: 89px;
  background-size: contain;
  margin-top: 170px;
  padding-top: 90px;
  background-repeat: no-repeat;
  position: relative;

}

.menu_button_new_game {
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 15px;
  background-color: #bd9952;
  width: 300px;
    /*transform: translate(-50%, -50%);*/
}
.menu_button_new_game:hover {
    background-color: #434a31;
    color: aliceblue;
    -webkit-transition: 1s;
    transition: 1s;
}

.tile {
  display: -webkit-inline-flex;
  display: inline-flex;
}
.rock {
  background-image: url("https://dmitry-skrypka.github.io/Incoder/tiles/rock.png");
}
.grass {
  background-color: #1c8a1c;
}
.tree {
  background-image: url("https://dmitry-skrypka.github.io/Incoder/tiles/tree.png");
}
.fridge {
  background-image: url("https://dmitry-skrypka.github.io/Incoder/tiles/fridge.jpg");
  background-size: contain;
  background-repeat: no-repeat;
}

